.container
{
	color: white;
	display: grid;
	grid-template-columns: 50% 50%; 
	background: rgba(78, 218, 204, 1);
	margin-top: 0px;
	padding: 30px;
	font-size: 20px;
	border-radius: 20px;
}

.container .nav
{
	display: grid;
	grid-template-columns: 30% 30% 30%; 
	float: left;
	font-size: 1.5rem;
	grid-gap: 10px;

}

.home 
{
	padding-left: 10px;
	padding-right: 10px;
}

.home:hover
{
	color: blue;
}

p
{
	font-size: 18px;
	color: white;
}

.home p:hover
{
	color: blue;
}


@media screen and (min-width: 800px) {
	.container
	{
		grid-template-columns: 70% 30%; 
		padding: 80px;
		font-size: 1rem;
	}

	.home 
	{
		padding-left: 30px;
		padding-right: 30px;
	}

	p{
		font-size: 24px;
	}
}

